.login-container {
    display: flex;
    height: 100%;
    overflow: hidden;
}

.showing {
    flex: 8 1 !important;
    -webkit-filter: brightness(100%) blur(0px) !important;
            filter: brightness(100%) blur(0px) !important;
}

/*Avoid Chrome Display None Stutter */
.showRight {
    height: 45% !important;
    width: 60% !important;
    opacity: 1 !important;
}

.showingInfo {
    opacity: 1 !important;
    transition: opacity 0.5s ease-in-out 0.5s !important;
}

.showingStar-1 {
    opacity: 1 !important;
    transition: opacity 0.5s ease-in-out 0.25s !important;
}
.showingStar-2 {
    opacity: 0.6 !important;
    transition: opacity 0.5s ease-in-out 0.65s !important;
}
.showingStar-3 {
    opacity: 0.4 !important;
    transition: opacity 0.5s ease-in-out 0.75s !important;
}

.showingLogo {
    right: 25px !important;
}

.main {
    opacity: 0;
    transition: opacity 0.15s ease-in-out 0s;
    padding-left: 5%;
}

/* LEFT PAGE CSS*/

.login-left {
    flex: 1 1;
    background: #d4e157;
    position: relative;
    z-index: 2;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
    font-family: 'Roboto Mono', monospace;
    transition: all 0.5s;
}

.login-subtitle-1 {
    position: absolute;
    font-size: calc(1vw + 5px);
    color: rgb(99, 99, 99);
