.star-show {
    width: 20vw;
    height: 20vw;
    max-width: 300px;
    max-height: 300px;
    position: absolute;
    top: 0;
    left: 0;
}

.stars {
    width: 100%;
    height: 100%;
}

.shooting-star{
    position: absolute;
}

.star-1 {
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.15s ease-in-out 0s;
}

.star-2 {
    width: 60%;
    height: 60%;
    top: calc(6vw);
    opacity: 0;
    transition: opacity 0.15s ease-in-out 0s;
}

.star-2 .trail {
    border-top-width: 0.8vw;
}

.star-2 .star {
    font-size: 3.5vw;
}

.star-3 {
    width: 30%;
    height: 30%;
    left: calc(3vw);
    top: -5px;
    opacity: 0;
    transition: opacity 0.15s ease-in-out 0s;
}

.star-3 .trail {
    border-top-width: 0.4vw;
}

