    flex-direction: row;
}

.appNums {
    margin: 1rem;
    display: flex;
    flex-direction: row;    
    flex: 1 1;
}

.acceptReject {
    margin-right: 1rem;
    display: flex;
    flex-direction: column;
}

.deferWait {
    display: flex;
    flex-direction: column;
}

.innerNums {
    margin: 1rem;
}

.appPercent {
    flex: 2 1;
    display: flex;
    flex-direction: column;
    margin: 1rem;
    font-size: 1rem;
}

.innerPercent {
    font-size: 5rem;
    position: relative;
    height: 100%;
}

.newAppWrap {
    margin: 1rem;
    margin-right: 1rem;
    max-width: 33vw;
    right: 10%;
    flex: 2 1;
    display: flex;
    border-radius: 10px;
}

.newAppWrap:hover {
    background-color: #D4E157;
    transition: 0.5s;
    cursor: pointer;
}

.newApp {
    font-size: 2rem;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    flex: 2 1;
    text-align: justify;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: 10px;
}

.newAppBtn {
    flex: 2 1;
    margin-left: 1rem;
    margin-right: 1rem;
}


.newAppBtn i {
    font-size: 3rem;
    top: 50%;
    transform: translateY(75%);
}

.newAppBtn i:hover{
    color: rgb(201, 115, 252);
    transition: 0.25s;
}

.appsList {
    background-color: rgb(171, 225, 247);
    border-radius: 10px;
    margin: 1rem;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: scroll;
    height: 100%;
    max-height: 66vh;
}

.application {
    margin: 1rem;
    border-radius: 10px;
    background-color: white;
}

.appModalForm {
    display: flex;
    flex-direction: column;
}

.newAppSchool {
    display: flex;
    flex-direction: row;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 10px;
}

.newAppSchoolText {
    flex: 3 1;
}

.newAppSchoolInput {
    flex: 3 1;
    margin-right: 1rem;
}

.newAppSchoolInput input {
    width: 100%;
}

.newAppStatus {
    display: flex;
    flex-direction: row;
    margin-left: 1rem;
    margin-right: 1rem;
    margin-bottom: 10px;
}

.newAppStatusText {
    flex: 3 1;
}

.newAppStatusSelect {
    flex: 1 1;
    margin-right: 1rem;
}
.applicationWrap {
    padding: 1rem;
    display: flex;
    flex-direction: row;
}

.applicationLogo {
    margin-left: 1rem;
    flex: 1 1;
    background: url(/static/media/schoolAppLogo.abb607ba.png);
    background-repeat: no-repeat;
    background-size: contain;
}

.appNameStatusWrap {
    flex: 2 1;
}

.appSchoolName {
    margin-bottom: 10px;
    font-size: 1.5rem;
}

.appStatusWrap {
    display: flex;
    flex-direction: row;
}

.appStatus {
    font-size: 1rem;
    flex: 1 1;
}

.appStatusFlag {
    font-size: 1rem;
    flex: 3 1;
    display: flex;
    flex-direction: row;
}

.appStatusFlag i {
    color: rgba(255,0,0, 0.5);
}



.editAppLogo {
    flex: 1 1;
}

.editAppLogo i {
    float: right;
    margin-right: 10%;
    font-size: 2rem;
}

.fa-check-square {
    font-size: 5rem;
}

.editAppLogo i:hover {
    color: orange;
    cursor: pointer;
    transition: 0.5s;
}

.tooltip {
    margin-left: 1rem;
    font-style: italic;
    color: rgba(255, 0, 0, 0.5);
}


.appmodal-background {
    width: 100%;
    height: 100%;
    pointer-events: none;
    opacity: 0.8;
    background: #222;
}

.appmodal {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1500;
}

.appmodal-panel {
    font-family: 'Roboto Condensed', monospace;
    background: #222;
    color: #D4E157;
    width: 40%;
    max-width: 500px;
    min-width: 300px;
    flex: 1 1;
    padding: 10px;
}

.appmodal-content {
    width: 100%;
    font-size: calc(0.3vw + 12px);
    padding: 10px;
}

.appmodal-confirm {
    margin-right: 10px;
}

.appmodal-btns {
    float: right;
    display: flex;
    padding: 10px;
}

.appmodal-btns > div {
    border-radius: 5px;
    flex: 1 1;
    border: 1px solid #D4E157;
    padding: 3px 10px;
    text-align: center;
    transition: all 0.25s;
    cursor: pointer;
}

.appmodal-btns > div:hover {
    transform: translateY(-5px);
    -webkit-box-shadow: 
    0px 3px #D4E157, /* gradient effects */
    0px 4px #D4E157
}

.appmodal-title {
    text-transform: uppercase;
    font-weight: bold;
    width: 100%;
    height: 20%;
    padding: 10px;
    font-size: calc(0.75vw + 22px);
}

.appmodal-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
